.FriendList {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
}

.item {
  display: inline-flex;
  align-items: center;
  width: 375px;
  background-color: #fff;
  margin-bottom: 16px;
  padding: 8px 20px;
  box-shadow: 0px 2px 1px rgba(0, 0, 0, 0.2), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 1px 3px rgba(0, 0, 0, 0.12);
  border-radius: 4px;
}
